import { Component, OnInit } from '@angular/core';
import { PayrollDerazonaDvrService, PayrollDerazonaPekerjaanService } from 'src/app/sdk/payroll/services';
import { DirektoriPegawaiMasterService, AbsensiPilotAircraftService, AbsensiPilotRonService } from 'src/app/sdk/hr/services';
import { Dvr, Pekerjaan } from 'src/app/sdk/payroll/models';
import { ActivatedRoute, Router } from '@angular/router';
import { Pegawai_master, Aircraft, Ron } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private payrollDerazonaDvrService: PayrollDerazonaDvrService,
        private payrollDerazonaPekerjaanService: PayrollDerazonaPekerjaanService,
        private absensiPilotAircraftService: AbsensiPilotAircraftService,
        private absensiPilotRonService: AbsensiPilotRonService,
        private routerActive: ActivatedRoute,
        private router: Router
    ) { }

    id_dvr: string = ''
    dataDvr: Dvr = {}
    dataPegawai: Pegawai_master = {}
    dataAircraft: Aircraft = {}
    dataRon: Ron = {}
    dataPekerjaan: Pekerjaan = {}

    ngOnInit() {
        this.routerActive.paramMap.subscribe(
            params => {
                if (params.get('id_dvr')) {
                    this.id_dvr = params.get('id_dvr')
                }

            }
        )

        this.getDetailDvr()
    }

    getDetailDvr() {
        this.payrollDerazonaDvrService.getPayrollDerazonaDvrsId(this.id_dvr).subscribe(
            data => {
                this.dataDvr = data.data;
                this.getPegawai(data.data.id_pegawai)
                this.getRon(data.data.ron_kode)
                this.getPekerjaan(data.data.id_pekerjaan)
                this.getAircraft(data.data.id_aircraft)
            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data
            }
        )
    }
    getRon(ron_kode) {
        this.absensiPilotRonService.getAbsensiPilotRonsId(ron_kode).subscribe(
            data => {
                this.dataRon = data.data
            }
        )
    }
    getPekerjaan(id_pekerjaan: string) {
        this.payrollDerazonaPekerjaanService.getPayrollDerazonaPekerjaansId(id_pekerjaan).subscribe(
            data => {
                this.dataPekerjaan = data.data
            }
        )
    }

    getAircraft(id_aircraft: string) {
        this.absensiPilotAircraftService.getAbsensiPilotAircraftsId(id_aircraft).subscribe(
            data => {
                this.dataAircraft = data.data
            }
        )
    }


    goToBack() {

        this.router.navigate(['/hr/kalender/pilot/dvr/list'])
    }

}
