import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { DvrRoutingModule } from './dvr-routing.module';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { DetailComponent } from './detail/detail.component';
import { NgZorroAntdModule, NzRadioModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';


@NgModule({
  declarations: [ListComponent, AddComponent, EditComponent, DetailComponent],
  imports: [
    CommonModule,
    DvrRoutingModule,
    NgZorroAntdModule,
    ReactiveFormsModule,
    FormsModule,
    NzRadioModule,
],
providers: [FormBuilderTypeSafe],
})
export class DvrModule { }
