import { Component, OnInit } from '@angular/core';
import { PayrollDerazonaDvrService, PayrollDerazonaPekerjaanService } from 'src/app/sdk/payroll/services';
import { NzNotificationService } from 'ng-zorro-antd';
import { DirektoriPegawaiMasterService, AbsensiPilotAircraftService, AbsensiPilotRonService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Dvr } from 'src/app/sdk/payroll/models';
import { Pegawai_master, Pekerjaan, Ron, Aircraft } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(

        private payrollDerazonaDvrService: PayrollDerazonaDvrService,
        private notification: NzNotificationService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private payrollDerazonaPekerjaanService: PayrollDerazonaPekerjaanService,
        private absensiPilotAircraftService: AbsensiPilotAircraftService,
        private absensiPilotRonService: AbsensiPilotRonService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,

    ) { }

    id_dvr: string = ''
    dataDvr: Dvr = {}

    listPegawai: Pegawai_master[] = []
    listPekerjaan: Pekerjaan[] = []
    listRon: Ron[] = []
    listAircraft: Aircraft[] = []


    ngOnInit() {
        this.id_dvr = this.activatedRoute.snapshot.paramMap.get('id_dvr')
        this.form()
        this.getPreeData();
        this.getData();

    }

    getPreeData() {
        this.spinner.show()
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data;
            }
        )

        this.payrollDerazonaPekerjaanService.getPayrollDerazonaPekerjaans().subscribe(
            data => {
                this.listPekerjaan = data.data;
            }
        )
        this.absensiPilotAircraftService.getAbsensiPilotAircrafts().subscribe(
            data => {
                this.listAircraft = data.data;
            }
        )
        this.absensiPilotRonService.getAbsensiPilotRons().subscribe(
            data => {
                this.listRon = data.data;
            }
        )
        this.spinner.hide()
    }

    getData() {
        this.spinner.show()
        this.payrollDerazonaDvrService.getPayrollDerazonaDvrsId(this.id_dvr).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }


    dataForm: FormGroupTypeSafe<Dvr>
    form() {
        this.dataForm = this.fb.group<Dvr>({
            id_aircraft: new FormControl('', [Validators.required]),
            id_pekerjaan: new FormControl('', [Validators.required]),
            ron_kode: new FormControl('', [Validators.required]),
            tanggal: new FormControl(''),
            jam_terbang: new FormControl(''),
            id_dvr: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            type_rate: new FormControl('', [Validators.required]),
            id_pegawai: new FormControl('', [Validators.required]),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataDvr = this.dataForm.value
        this.payrollDerazonaDvrService
            .putPayrollDerazonaDvrsId({ id: this.id_dvr, body: this.dataDvr })
            .subscribe(
                data => {

                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }



    goToBack() {

        this.router.navigate(['/hr/kalender/pilot/dvr/list'])
    }

}
